	module.exports = {"data":{"markdownRemark":{"html":"<p>Rough outline</p>\n<ul>\n<li>What is GraphQL</li>\n<li>Why GraphQL? As Gatsby runs on both server (at build time) &#x26; client, need way to specify which data is needed.</li>\n<li>Emphasize this is a <em>build-time only</em> use of GraphQL. You don’t need to run a GraphQL server in production. Convenient way to describe data requirements of component.</li>\n<li>Why query colocation rocks.</li>\n<li>\n<p>Some basic terminology </p>\n<ul>\n<li>Types based on file type + way data can be transformed</li>\n<li>Connections</li>\n<li>Shallow intro to how data layer works e.g. source and transformer plugins.</li>\n<li>Compare to Webpack loaders — like loaders except create schema that can then be queried.</li>\n</ul>\n</li>\n<li>Example queries showing off sorting, filtering, picking fields, programmatic transformations</li>\n<li>Link to some doc pages on advanced usages of GraphQL.</li>\n<li>iFrame of graphiql instance for this site running on Heroku so people can run live queries.</li>\n</ul>","excerpt":"Rough outline What is GraphQL Why GraphQL? As Gatsby runs on both server (at build time) & client, need way to specify which data is needed…","timeToRead":1,"frontmatter":{"title":"Querying with GraphQL"},"parent":{"__typename":"File","relativePath":"docs/querying-with-graphql.md"}}},"pathContext":{"slug":"/docs/querying-with-graphql/"}}

